 .LabelStyle {
    margin-top: 0;
    margin-bottom: 0.5em !important;
}
  
  .ant-form-item-label {
    display:flex;
    position: relative;
    width: 33.33333333%;
    text-align: left !important;
  }
  
  
  .ant-form-item-control-wrapper {
    width: 66.66666667%;
    display: inline-flex;
    line-height: normal;
  }
  
  .container {
    text-align: center;
    padding: 20px;
    
  }
  
  .signIn-form-container {
    margin: 0 !important;
    width: 100%;
    text-align: left;
    margin-top: 65px;
    padding:20%;
  }
  
  .field-container {
    height: 65px;
    text-align:left;
    margin-left: 0;
  }
  
  .submit-container {
    text-align: center;
    margin-top: 25px;
  }
  
  .signIn-form-container .ant-btn-primary{
    color: #fff;
    background-color:rgb(212, 56, 13,0.8);
    border-color:rgb(247, 125, 94);
  }
  .signIn-form-container .ant-btn-primary:hover, .signIn-form-container .ant-btn-primary:active{
    background-color:rgb(247, 125, 94);
    border-color:rgb(247, 125, 94);
  }

  .signIn-form-container .ant-btn-link {
    color: grey;}

  .register-container{
    text-align: end;
  }


  .register-container .ant-btn-link:hover{
    color: #08979c
  }

.seperateButton {
  color: grey;}

   