.work_title {
    font-family: MaisonNeue-Bold;
    font-size: 36px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.11;
    letter-spacing: normal;
    text-align: center;
    color: var(--dark);
    text-align: left;
    margin-bottom: 24px;
  }
  
  /* .grid-item .bdgBJg.bdgBJg {
    margin: 15px;
  } */

  /* 做隔离和分区 这里导致了button点击变形 
  .ant-btn:active > span {
    position: absolute;
  }*/
  
  .grid:after {
    content: "";
    display: block;
    clear: both;
  }
  .grid-item,
  .grid-sizer {
    width: 1%;
  }
  
  .grid-item {
    float: left;
    height: 300px;
    width: 500px;
    overflow: hidden;
    margin-right: 24px;
    margin-bottom: 24px;
    overflow: hidden;
    position: relative;
  }
  
  .grid-item img {
    min-width: 100%;
    min-height: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translateY(-50%) translateX(-50%);
    transform: translateY(-50%) translateX(-50%);
    -ms-transform: translateY(-50%) translateX(-50%);
    overflow: hidden;
    transition: all 0.5s ease-in-out;
  }
  .grid-item img:hover {
    -ms-transform: translateY(-50%) translateX(-50%) scale(1.1); /* IE 9 */
    -webkit-transform: translateY(-50%) translateX(-50%) scale(1.1); /* Safari 3-8 */
    transform: translateY(-50%) translateX(-50%) scale(1.1);
  }
  .grid-item-button {
    width: 30%;
    text-align: left;
  }
