.sideBarContainer{
    width: 100%;
    background-color:white;
}

.sideBarContainer .ant-menu-item-selected a,
.sideBarContainer .ant-menu-item-selected a:hover{
    color:rgba(96, 166, 175);
}
.sideBarContainer .ant-menu-submenu-selected{
    color:rgba(96, 166, 175);
}

.sideBarContainer .ant-menu-item:hover, 
.sideBarContainer .ant-menu-item-active a, 
.sideBarContainer .ant-menu:not(.ant-menu-inline) 
.sideBarContainer .ant-menu-submenu-open a, 
.sideBarContainer .ant-menu-submenu-active a, 
.sideBarContainer .ant-menu-submenu-title a,
.sideBarContainer .ant-menu-submenu-title:hover{
    color:rgba(96, 166, 175)
}

/**--------问题 这里如何修改？:hov 和 cls 区别在哪里 ---------***/
.sideBarContainer  .ant-menu-submenu-arrow{
    color:rgba(96, 166, 175)
}

.ant-menu-vertical .ant-menu-item::after, 
.ant-menu-vertical-left .ant-menu-item::after, 
.ant-menu-vertical-right .ant-menu-item::after, 
.ant-menu-inline .ant-menu-item::after{
    border-right: 3px solid rgba(96, 166, 175);
}