  .HomePageHero{
    background-image: url("../../../Arts/Blue_Airplane.jpg");
    background-size:cover; 
    background-repeat: no-repeat;
    background-position: center ; 
    height:600px;
    width: 100%;
    padding:0;
    text-align: center;
  }
  .HomePageHero h1{
    font-size: xx-large;
    font-weight: 900;
    font-style: italic;
    padding-top: 7vh;
  }

.Introduce{
  margin-top: 10vh;
  margin-bottom: 10vh;
  padding-left: 5vh;
  padding-right:5vh;
  
}
  
.Introduce .text{
  text-align: center;
}

.HPContainer .IntroList{
  margin-top:3vh;
  margin-bottom: 10vh;
  margin-left:10% !important;
  margin-right:10% !important;
  padding-left: 20px;
  padding-right:20px;
}


.IntroList .ant-card-cover{
  padding-left: 10%;
  padding-right:10%;
  padding-top: 10%;
}

.IntroList .ant-card-body{
  text-align: center;
}

.stepContainer{
  margin: 2vh 5vh;
}


.stepTitle{
  display: flex;
  flex-direction: column;
  text-align: center;
}

.stepTitle h3{
  color:rgba(0, 0, 0, 0.45)
}

.stepofProcess{
  padding:0 2vh;
  font-size: xx-large;
  font-weight: 800;
}

.userToKnow{
  margin-top: 10vh;
}
.toKnowTitle{
  display: flex;
  flex-direction: column;
  text-align: center;
}

.toKnowTitle h3{
  color:rgba(0, 0, 0, 0.45)
}

/**-----------step 颜色 icon 和 尾巴 ---------------**/

.stepContainer .stepofProcess .ant-steps-icon svg {
  fill:rgba(96, 166, 175);
}

.stepContainer .ant-steps-item-finish > .ant-steps-item-container > .ant-steps-item-content > .ant-steps-item-title::after {
  background-color:rgba(96, 166, 175, 0.7);
}

