

  .TopBarContainer{
    justify-content:space-between;
    padding-top:10px;
    padding-right:30px;
    display:flex;
    flex-direction: row;
  }

  .ant-layout-header{
    height:100% !important; 
    background-color:#fafafa!important;
    /*opacity: 0.6; 包括字也不透明了 rog只是背景色透明度*/
    padding:0 !important; /*这样两边不会有黑色底色透露出来 黑底色写在dashboard中*/
  }

.SignInLinkBox .ant-avatar{
  padding:1px 0 0 0 !important;
}

.SignInLinkBox .ant-avatar-lg {
  line-height: normal;
}

.SignInLinkBox .ant-avatar-string{
  position: absolute;
  left: 50%;
  top: 28%;
  transform-origin: 0 center;
}

div .logo{
  background-image: url("../../Arts/NewLogo.png");
  background-size:cover; 
  background-repeat: no-repeat;
  background-position: center ; 
  width:60px;
  height:60px;
  padding:0;
}

.logoBox{
  display:flex;
  flex-direction: row;
  width:20%;
  justify-content: center;
}
.logoText{
  font-weight: 700;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-style: italic;
}

/*----Top bar menu 选中 划过变色------*/
.TopBarContainer .ant-menu-horizontal > .ant-menu-item:hover, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-submenu:hover, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-item-active, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-submenu-active, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-item-open, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-submenu-open, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-item-selected, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-submenu-selected{
  color: rgba(96, 166, 175);
  border-bottom:  2px solid transparent; 
}

.TopBarContainer .ant-menu-horizontal > .ant-menu-item a:hover, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-submenu a:hover, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-item-active a, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-submenu-active a, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-item-open a, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-submenu-open a, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-item-selected a, 
.TopBarContainer .ant-menu-horizontal > .ant-menu-submenu-selected a{
  color: rgba(96, 166, 175);
}
/*----登陆按钮左右两块平行排布-------*/
.SignInLinkBox{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.SignInLinkBox .text{
  margin-bottom: 0;
  margin-right:3vh;
}


