
.ant-layout .RegisterContainer{
    background-image: url("../../Arts/yellow_Box.jpg");
    background-size:cover; 
    background-repeat: no-repeat;
    background-position: center ; 
    height:100%;
    width: 100%;
    padding:0;
}
.alterRegister{
    background-color: transparent;
}

.alterRegister .ant-alert-info{
    border:none;
    width:100%;
    opacity:0.8;
}

.RegisterFormContainer{
