
.returnToHome{
  text-align: end
}  /*在signin中有效 在registerForm中无效 为啥*/

.ant-col{
    display:flex;
  }

  .LabelStyle {
    margin-top: 0;
    margin-bottom: 0.5em !important;
}
  .ant-form-item-label {
    display:flex;
    position: relative;
    width: 33.33333333%;
    text-align: left !important;
  }
  
  
  .ant-form-item-control-wrapper {
    width: 66.66666667%;
    display: inline-flex;
    line-height: normal;
  }
  
  .Register-form-container{
    width:100%;
  }
  .field-container {
    height: 65px;
    text-align:left;
    margin-left: 0;
  }
  
  .submit-container {
    text-align: center;
    margin-top: 25px;
  }

  
  .Register-form-container >.submit-container >.ant-btn-primary{
    color: #fff;
    background-color:rgb(212, 56, 13,0.8);
    border-color:rgb(247, 125, 94);
  }
  .Register-form-container > .submit-container > .ant-btn-primary:hover{
    background-color:rgb(247, 125, 94);
    border-color:rgb(247, 125, 94);
  }

  .Register-form-container > .submit-container > .ant-btn-primary:active{
    background-color:rgb(247, 125, 94);
    border-color:rgb(247, 125, 94);
  }

  .Register-form-container .ant-btn-link:hover{
    color: #08979c
    }

  .Register-form-container .ant-btn-link{
      color: grey;
  }
  /*hover link 设置一个全网站通用的颜色 通用*/